<template>
  <v-container fluid>
    <v-card
      :loading="
        !stepsDisableLoadingAnimation.includes(step) && $store.getters.loading
      "
    >
      <v-stepper flat non-linear :value="step" ref="stepper">
        <v-stepper-header>
          <template v-for="(s, index) in steps">
            <v-stepper-step
              :key="index * 2"
              :step="index + 1"
              :editable="canChangeStep"
              @click="handleStepClick(index + 1)"
            >
              {{ s }}
            </v-stepper-step>
            <v-divider
              v-if="index < steps.length - 1"
              :key="index * 2 + 1"
            ></v-divider>
          </template>
        </v-stepper-header>
      </v-stepper>
    </v-card>

    <confirmation-dialog
      :show="showDialog"
      @confirm="confirmChangeStep"
      @cancel="cancelChangeStep"
      @confirm-save="confirmSaveAndChangeStep"
    />

    <configuracoes
      v-if="Number(step) === 1"
      v-show="!$store.getters.loading || $store.getters.loadingFromReload"
      @saved="saved"
      @changed="changed"
      @next="nextStep"
      @navigate="handleStepClick"
      :id="idConfiguracao"
      :ref="steps[0]"
    />
    <quantidades
      v-if="Number(step) === 2"
      v-show="!$store.getters.loading || $store.getters.loadingFromReload"
      :id="idConfiguracao"
      @saved="saved"
      @changed="changed"
      @next="nextStep"
      :ref="steps[1]"
    />
    <constantes
      v-if="Number(step) === 3"
      v-show="!$store.getters.loading || $store.getters.loadingFromReload"
      :id="idConfiguracao"
      @saved="saved"
      @changed="changed"
      @next="nextStep"
      :ref="steps[2]"
    />
    <restricoes
      v-if="Number(step) === 4"
      v-show="!$store.getters.loading || $store.getters.loadingFromReload"
      :id="idConfiguracao"
      @saved="saved"
      @changed="changed"
      @next="nextStep"
      :ref="steps[3]"
    />
    <especiais
      v-if="Number(step) === 5"
      v-show="!$store.getters.loading || $store.getters.loadingFromReload"
      :id="idConfiguracao"
      @saved="saved"
      @changed="changed"
      @next="nextStep"
      :ref="steps[4]"
    />
    <minimos-maximos
      v-if="Number(step) === 6"
      v-show="!$store.getters.loading || $store.getters.loadingFromReload"
      :id="idConfiguracao"
      @saved="saved"
      @changed="changed"
      @next="nextStep"
      :ref="steps[5]"
    />
    <horarios
      v-if="Number(step) === 7"
      :id="idConfiguracao"
      @saved="saved"
      @changed="changed"
      @next="nextStep"
      :ref="steps[6]"
    />

    <div class="fixed-buttons">
      <v-btn color="primary" :disabled="Number(step) === 7" @click="nextStep"
        ><v-icon> mdi-menu-right-outline </v-icon></v-btn
      >
      <v-badge color="red" v-if="hasUnsavedChanges">
        <v-btn
          color="primary"
          :disabled="Number(step) === 7"
          @click="handleSave"
          >Salvar</v-btn
        >
      </v-badge>
      <v-btn
        v-else
        color="primary"
        :disabled="Number(step) === 7"
        @click="handleSave"
        >Salvar</v-btn
      >
      <v-btn color="primary" @click="handleRefreshClick"
        ><v-icon> mdi-refresh </v-icon></v-btn
      >
      <v-btn color="primary" :disabled="Number(step) === 1" @click="prevStep"
        ><v-icon> mdi-menu-left-outline </v-icon></v-btn
      >
    </div>
  </v-container>
</template>

<script>
export default {
  name: "PassosHorario",
  components: {
    Configuracoes: () => import("@/views/Configuracoes.vue"),
    Quantidades: () => import("@/views/Quantidades.vue"),
    Constantes: () => import("@/components/TabelaConstantes.vue"),
    Restricoes: () => import("@/components/Restricoes.vue"),
    Especiais: () => import("@/components/Especiais.vue"),
    MinimosMaximos: () => import("@/components/MinimosMaximos.vue"),
    Horarios: () => import("@/components/Horarios.vue"),
    ConfirmationDialog: () => import("@/components/ConfirmationDialog.vue"),
  },
  props: ["idConfiguracao"],
  data() {
    return {
      step: 1,
      hasUnsavedChanges: false,
      showDialog: false,
      pretendedStep: null,
      steps: [
        "Estrutura da Escola",
        "Aulas/Matérias",
        "Aulas constantes",
        "Restrições",
        "Regras de preenchimento",
        "Mínimos/Máximos",
        "Horários",
      ],
      stepsDisableLoadingAnimation: [7],
      changeStepConfirmationMessage:
        "Você tem alterações não salvas, que serão perdidas. Tem certeza que deseja mudar de etapa?",
      refreshConfirmationMessage:
        "Você tem alterações não salvas, que serão perdidas. Tem certeza que deseja recarregar?",
    };
  },
  methods: {
    async handleSave(reload = true) {
      const stepName = this.steps[this.step - 1];
      if (this.$refs[stepName] != null) {
        await this.$refs[stepName].save(reload);
      }
    },
    handleRefreshClick() {
      if (this.hasUnsavedChanges) {
        this.$store.dispatch(
          "setConfirmationDialogMessage",
          this.refreshConfirmationMessage
        );
        this.pretendedStep = "refresh";
        this.showDialog = true;
      } else {
        this.refreshStep();
      }
    },
    refreshStep() {
      const stepName = this.steps[this.step - 1];
      if (this.$refs[stepName] != null) {
        const isReload = true;
        this.$refs[stepName].load(isReload);
      }
      this.pretendedStep = null;
    },
    handlePageExit() {
      if (this.hasUnsavedChanges) {
        this.showDialog = true;
        return false;
      }
      this.beforeChangeStep();
      return true;
    },
    beforeChangeStep() {
      const stepName = this.steps[this.step - 1];
      if (this.$refs[stepName] != null && this.$refs[stepName].beforePageExit) {
        this.$refs[stepName].beforePageExit();
      }
    },
    handleStepClick(e) {
      if (this.hasUnsavedChanges && e !== this.step) {
        this.$store.dispatch(
          "setConfirmationDialogMessage",
          this.changeStepConfirmationMessage
        );
        this.showDialog = true;
        this.pretendedStep = e;
      } else {
        this.changeToStep(e);
      }
    },
    confirmChangeStep() {
      if (this.pretendedStep === "refresh") {
        this.refreshStep();
      } else {
        this.step = this.pretendedStep;
        this.$emit("confirm-navigation");
      }
      this.showDialog = false;
      this.saved();
    },
    async confirmSaveAndChangeStep() {
      const reload = false;
      await this.handleSave(reload);
      this.confirmChangeStep();
    },
    saved() {
      this.hasUnsavedChanges = false;
    },
    changed() {
      this.hasUnsavedChanges = true;
    },
    nextStep() {
      if (this.step < this.steps.length) {
        this.handleStepClick(this.step + 1);
      }
    },
    prevStep() {
      if (this.step > 1) {
        this.handleStepClick(this.step - 1);
      }
    },
    changeToStep(step) {
      this.beforeChangeStep();
      this.step = step;
    },
    cancelChangeStep() {
      this.pretendedStep = null;
      this.showDialog = false;
      this.forceStepperValue();
    },
    forceStepperValue() {
      this.$refs.stepper.internalLazyValue = this.step;
    },
  },
  computed: {
    canChangeStep() {
      return !this.$store.state.hasUnsavedChanges;
    },
  },
};
</script>

<style>
.fixed-buttons {
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: flex;
  flex-direction: row-reverse;
}

.fixed-buttons v-btn {
  margin-left: 8px;
}

.v-badge__badge {
  inset: auto auto calc(100% - 10px) calc(100% - 10px) !important;
}
</style>
